.App {
  text-align: center;
}

.App-header {
  background-color: #393f4a;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.slider-nav a:hover{
  opacity: 1;
}

.Movie-container {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
}

.shower {
  visibility: visible;
}

.Movie-wrapper {
  width: 300px;
  background-color: antiquewhite;
  color: black;
  border-radius: 20px;
  transition: transform .2s;
}

.Cards{
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
}

.Movie-wrapper:hover{
  transform: scale(108%);
  .Movie-image{
    filter: blur(20px);
  }
  .Movie-title{
    scale: 100%;
  }
}

.Movie-image{
  width: 100%;
  height: 100%;
  border-radius: 20px;
  transition : filter .2s
}

.Movie-search {
  height: 60px;
  font-size: 24px;
  margin-bottom: 40px;
  padding: 8px;
  font-weight: bold;
  width : 350px;
  color: black;
}

.Movie-title {
  scale: 0;
  margin-top: 12px;
  color: white;
  font-weight: bold;
  font-size: 2rem;
  position: absolute;
  bottom: 45%;
  transition: scale .2s;
}

.dePad{
  padding-top: 56.25%;
}

.grad {
  font-size: 72px;
  background: -webkit-linear-gradient(#eed202, #ffff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

@media screen and (max-width: 640px){
  .Movie-wrapper{
    width: 200px;
  }
  .Movie-container{
    gap: 20px;
  }
  .Movie-search{
    height: 50px;
    width: 350px;
  }
  .Movie-title{
    font-size: 1.5rem
  }
}